<template>
  <h1 class="title">Plane deine Module</h1>
  <label>
    Letztes erfolgreich abgeschlossenes Semester
    <select v-model="lastSemesterNumber">
      <option
        v-for="semester in semesters"
        :key="semester.number">
        {{ semester.number }}
      </option>
    </select>
  </label>
  <div class="columns">
    <div class="column semester" v-for="semester in semesters" :key="semester.name">
      <Semester
        :number="semester.number"
        v-model:modules="semester.modules"
        :all-modules="modules"
      ></Semester>
    </div>
    <div class="column add-semester">
      <button class="p-2 add-semester-btn has-text-weight-bold" v-on:click="addSemester">+</button>
    </div>
  </div>
  <div class="columns">
    <div class="column">
      <article>
        <h2 class="subtitle">Overview ECTS</h2>
        <table>
          <tbody>
          <tr
            v-for="category in mappedCategories"
            :key="category.name"
            v-bind:class="category.categoryClass">
            <td style="vertical-align:bottom;padding-right:1em;text-align:end">
              {{ category.name }}
            </td>
            <td style="padding-top:8px">
              <BeautifulProgressIndicator
              :required="category.required_ects"
              :earned="category.earnedCredits"
              :planned="category.plannedCredits"
              :color="category.color"
              ></BeautifulProgressIndicator>
            </td>
          </tr>
          <tr>
            <td class="p-2">Total: TODO</td>
            </tr>
          </tbody>
        </table>
      </article>
    </div>
    <div class="column">
      <article>
        <h2 class="subtitle">Course focus</h2>
        <div class="columns is-multiline">
          <div v-for="focus in mappedFocuses"
            :key="focus.name" class="column is-full">
            <Focus
              :name="focus.name"
              :allModules="focus.modules"
              :filteredModules="focus.filteredModules"
            ></Focus>
          </div>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import Semester from '../components/Semester.vue';
import Focus from '../components/Focus.vue';
import BeautifulProgressIndicator from '../components/BeautifulProgressIndicator.vue';

const BASE_URL = 'https://raw.githubusercontent.com/jeremystucki/ost-planer/1.0/data';
const ROUTE_MODULES = '/modules.json';
const ROUTE_CATEGORIES = '/categories.json';
const ROUTE_FOCUSES = '/focuses.json';
export default {
  name: 'Home',
  data() {
    return {
      semesters: [],
      modules: [],
      categories: [],
      focuses: [],
      lastSemesterNumber: 0,
    };
  },
  computed: {
    mappedCategories() {
      return this.categories.map((category) => ({
        earnedCredits: this.getEarnedCredits(category),
        plannedCredits: this.getPlannedCredits(category),
        color: `#${((1 << 24) * Math.random() | 0).toString(16)}`, // TODO
        ...category,
      }));
    },
    plannedModules() {
      return this.semesters
        .flatMap((semester) => semester.modules);
    },
    mappedFocuses() {
      const plannedModuleNames = this.plannedModules.map(module => module.name);
      return this.focuses.map((focus) => ({
        ...focus,
        filteredModules: focus.modules.filter(
          (moduleName) => !plannedModuleNames.includes(moduleName),
        ),
      }));
    },
    totalPlannedEcts() {
      return this.getTotalEcts(true);
    },
    totalEarnedEcts() {
      return this.getTotalEcts();
    },
  },
  components: { Semester, Focus, BeautifulProgressIndicator },
  methods: {
    loadModules() {
      fetch(`${BASE_URL}${ROUTE_MODULES}`)
        .then((response) => {
          if (response.ok) {
            response.json()
              .then((modules) => {
                this.modules = modules;
                this.restorePlanFromUrl();
                this.loadCategories();
                this.loadFocuses();
              });
          }
        });
    },
    loadCategories() {
      fetch(`${BASE_URL}${ROUTE_CATEGORIES}`)
        .then((response) => {
          if (response.ok) {
            response.json()
              .then((categories) => {
                this.categories = categories;
              });
          }
        });
    },
    loadFocuses() {
      fetch(`${BASE_URL}${ROUTE_FOCUSES}`)
        .then((response) => {
          if (response.ok) {
            response.json()
              .then((focuses) => {
                this.focuses = focuses;
              });
          }
        });
    },
    restorePlanFromUrl() {
      const path = window.location.hash;
      if (path.startsWith('#/plan/')) {
        this.semesters = path
          .slice(7)
          .split('-')
          .map((semester, index) => ({
            number: index + 1,
            modules: semester
              .split('_')
              .map((moduleId) => {
                const newModule = this.modules.find((module) => module.id === moduleId);
                // eslint-disable-next-line no-console
                if (newModule == null) console.warn(`Module with id: ${moduleId} could not be restored`);
                return newModule;
              })
              .filter((module) => module != null),
          }));
      }
    },
    updateUrlFragment() {
      window.location.hash = `plan/${this.semesters
        .map((semester) => semester.modules.map((module) => module.id)
          .join('_'))
        .join('-')}`;
    },
    getPlannedSemesterForModule(moduleName) {
      return this.semesters.find(
        (semester) => semester.modules.some(module => module.name === moduleName),
      )?.number;
    },
    getEarnedCredits(category) {
      return this.semesters
        .filter((semester) => semester.number <= this.lastSemesterNumber)
        .flatMap((semester) => semester.modules)
        .filter((module) => module.categories.includes(category.name))
        .reduce((previousTotal, module) => previousTotal + module.ects, 0);
    },
    getPlannedCredits(category) {
      return this.semesters
        .filter((semester) => semester.number > this.lastSemesterNumber)
        .flatMap((semester) => semester.modules)
        .filter((module) => module.categories.includes(category.name))
        .reduce((previousTotal, module) => previousTotal + module.ects, 0);
    },
    getTotalEcts(includePlanned = false) {
      return this.semesters
        .filter((semester) => semester.number <= this.lastSemesterNumber || includePlanned)
        .flatMap((semester) => semester.modules)
        .reduce((previousTotal, module) => previousTotal + module.ects, 0);
    },
    addModule(semesterNumber, moduleName) {
      const module = this.modules.find((item) => item.name === moduleName);
      this.semesters[semesterNumber - 1].modules.push(module);
      this.updateUrlFragment();
    },
    removeModule(semesterNumber, modulesIndex) {
      this.semesters[semesterNumber - 1].modules.splice(modulesIndex, 1);
      this.updateUrlFragment();
    },
    addSemester() {
      this.semesters.push({
        number: this.semesters.length + 1,
        modules: [],
      });
    },
  },
  mounted() {
    this.loadModules();
    window.addEventListener('hashchange', this.restorePlanFromUrl);
  },
};
</script>
